
.ready {
    width: 100%;
    height: 200px;
    background-color: #F67E7E;
    position: relative;
}

.bottom-left {
    position: absolute;
    left: 0;
    bottom: 0;
}

.ready-container {
    position: absolute;
    display: flex;
    width: 917px;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    justify-content: space-between;
}

.ready-container h3 {
    font-family: livvic-bold;
    font-size: 48px;
    line-height: 48px;
    color: #012F34;
    margin: 0;
}

.ready-contact-us-btn {
    font-family: livvic-semibold;
    font-size: 18px;
    background-color: transparent;
    color: #012F34;
    height: auto;
    padding: 10px 33px;
    border-radius: 2em;
    border: 2px solid #012F34;
}