.app-footer {
    background-color: #002529;
    height: 196px;
    width: 100%;
}

.footer-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 48px;
    height: auto;
}

.left-side {
    display: flex;
}

.footer-logo-nav {
    margin-right: 125px;
}

.left-side .logo {
    margin-bottom: 25px;
}

.left-side .nav span {
    font-family: livvic-semibold;
    font-size: 18px;
    line-height: 28px;
    color: white;
    margin-right: 40px;
}

.loc {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-family: livvic-semibold;
    font-size: 15px;
    line-height: 25px;
    color: rgba(255, 255, 255, 0.6);
}

.app-footer .right-side {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transform: translateY(-0px);
}

.app-footer .right-side .social-icons {
    text-align: right;
    margin-bottom: 35px;
}

.app-footer .right-side p {
    font-family: livvic-semibold;
    font-size: 15px;
    line-height: 25px;
    color: rgba(255, 255, 255, 0.6);
}