.story {
    text-align: center;
}

.story-txt {
    font-family: livvic-semibold;
    font-size: 15px;
    color: white;
    line-height: 25px;
    transform: translateY(-24px);
    /* margin-bottom: 15px; */
}

.story-name {
    font-family: livvic-bold;
    font-size: 18px;
    line-height: 28px;
    color: #79C8C7;
    margin-bottom: 32px;
}

.story img {
    height: 62px;
    width: 62px;
    border-radius: 50%;
    border: 2px solid #C4FFFE;
    object-fit: cover;
}