
.pros {
    width: 100%;
    height: 684px;
    background-color: #012F34;
    position: relative;
}

.pros .bottom-right-circle {
    position: absolute;
    bottom: 0;
    right: 0;
}

.pros-container {
    padding-top: 140px;
    height: auto;
    display: flex;
    justify-content: space-between;
}

.pros .left-side {
    display: flex;
    flex-direction: column;

}

.pros-title {
    font-family: livvic-bold;
    font-size: 48px;
    line-height: 48px;
    color: white;
}

.pros .right-side {
    transform: translateY(40px);
}
.pros .item {
    display: flex;
    margin-bottom: 40px;
}

.pros .item .icon {
    margin-right: 20px;
}

.pros .item .item-text {
    transform: translateY(10px);
}

.pros .item .item-title {
    font-family: livvic-bold;
    color: #F67E7E;
    margin-bottom: 20px;
}

.pros .item .item-info {
    font-family: livvic-semibold;
    color: white;
    font-size: 15px;
    line-height: 25px;
}