.success {
    width: 100%;
    height: 745px;
    background-color: #004047;
    position: relative;
}

.success .top-left {
    position: absolute;
    top: 0;
    left: 0;
}

.success .bottom-right {
    position: absolute;
    bottom: 0;
    right: 0;
}

.success .success-title {
    font-family: livvic-bold;
    font-size: 48px;
    line-height: 48px;
    color: white;
    text-align: center;
    padding-top: 140px;
} 

.success .success-title span {
    color: #79C8C7;
}

.story-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    height: auto;
    gap: 30px;
    margin-top: 56px;
}