.hero {
    position: relative;
    width: 100%;
    height: 700px;
    background-color: #014E56;
}

.hero .side-circle {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

.hero .bottom-circle {
    position: absolute;
    right: 0;
    bottom: 0;
}

.hero-header {
    display: flex;
    justify-content: space-between;
    padding-top: 77px;
}

.logo-nav {
    color: white;
    display: flex;
    align-items: center;
}

.logo-nav .logo {
    margin-right: 70px;
}

.logo-nav span {
    font-family: livvic-semibold;
    font-size: 18px;
    margin-right: 30px;
}

.hero .contact-us-btn {
    font-family: livvic-semibold;
    font-size: 18px;
    background-color: transparent;
    color: white;
    padding: 10px 33px;
    border-radius: 2em;
    border: 2px solid white;
}

.main-info {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
}

.big-slogan {
    font-family: livvic-bold;
    font-size: 100px;
    color: white;
    transform: translateX(-6px);
    margin: 0;
    line-height: 100px;
}

.big-slogan span {
    color: #F67E7E;
}

.right-side {
    display: flex;
    flex-direction: column;
    transform: translateY(25px);
}

.hero-info {
    font-family: livvic-semibold;
    font-size: 18px;
    color: white;
    margin-top: 75px;
}